button {
  width: 300px;
  height: 300px;
  padding: 15px 0;
  margin-left: 150px;
  margin-right: 150px;
  border: none;
  background: var(--color);
  border-radius: 150px;
  color: white;
  font-size: 42px;
  line-height: 24px;
  cursor: pointer;
}
button:hover {
  filter: brightness(85%);
}
button:active {
  filter: brightness(60%);
}

